import React from "react";
import img from "../assets/emma-gotland.jpg";

export default function About() {
  /*  const changeBackgroundColor = () => {
    const myColor = "#" + Math.floor(Math.random() * 16777215).toString(16);
    document.body.style.backgroundColor = myColor;
  }; */

  const randombg = () => {
    var random = Math.floor(Math.random() * 4) + 0;
    var bigSize = [
      'url("./assets/backgrounds/1.jpg")',
      'url("./assets/backgrounds/2.jpg")',
      'url("./assets/backgrounds/3.jpg")',
      'url("./assets/backgrounds/4.jpg")',
    ];
    document.body.style.backgroundImage = bigSize[random];
  };
  return (
    <div class="about-me-content">
      <article class="about-me">
        <section class="tex-about-me">
          <h1>Om mig</h1>
          <p>
            Mitt namn r Emma Lind och jag r en framtida systemutvecklare som
            studerar p Edugrade! Jag bor lantligt en bit utanfr Hudiksvall
            tillsammans med min sambo, tv dttrar och sibirkatten Bjran.
            Egentligen r jag inte infdd hlsing (utan medelpading) men
            integreras sakta men skert att ta riktig ostkaka och sga saker
            som chalur och joll.
          </p>
          <p>
            Nr jag inte har fullt upp med barn och hem (nr r det liksom) kan
            det hnda att jag hamnar p ngon metalspelning eller historisk
            marknad (grna en kombination). Men allra helst spenderas tiden
            hemma med familjen ptandes i trdgrden, strvandes i naturen eller
            fast i ett klurigt pusselspel.
          </p>
          <p>
            Tidigare i livet var den stora passionen blommor, s pass att jag
            utbildade mig till florist och arbetade som det ett tag. Efter
            flytten bestmde jag mig fr att skola om till systemutvecklare, men
            att binda buketter och kransar gr jag fortfarande p fritiden.
          </p>
          <p>
            Att brja programmera r bland det roligaste jag gjort, kanske fr
            att man fr kombinera sitt analytiska och kreativa sinne som allra
            bst. Jag ser verkligen fram emot att fortstta vxa och utvecklas i
            detta yrke!
          </p>
        </section>
        <img
          src={img}
          alt="jag p gotland"
          onClick={randombg}
        />
      </article>
    </div>
  );
}
